<template>
	<view class="about-container">

		<view class="header-section text-center">


		</view>

		<view class="content-section">
			<image style="width: 100%;height: 250rpx;" src="https://qkx.meooota.com/qkxupfile/bannerxdzx.jpg">
			</image>
			<view class="menu-list">
			    <view
			      v-for="(task, index) in taskList"
			      :key="index"
			      class="list-cell"
			      @click="handleTaskClick(task)"
			    >
			      <view class="menu-item-box">
			        <view>{{ task.taskName }}</view>
					<view style="margin-left: 15rpx;color: green;">+{{ task.taskPoint }}</view>
			        <view    
					:class="{ 'completed-status': task.finshStatus === '已完成', 'pending-status': task.finshStatus !== '已完成' }">{{ task.finshStatus }}</view>
			      </view>
			    </view>
			  </view>
		</view>

	</view>
</template>

<script>
	import {
		getUserTask
	} from '@/api/report'
	import storage from '@/utils/storage'
	import constant from '@/utils/constant'
	export default {
		data() {
			return {
				userId: storage.get(constant.userId),
				taskList: []
			}
		},
		onShow() {
			this.getUserTask(this.userId)
		},
		methods: {
			getUserTask(userId) {
				getUserTask(userId).then(res => {
					this.taskList = res.data


				});
			},
			handleTaskClick(task) {
			      if (task.finshStatus === '去完成') {
					   uni.switchTab({
					   	url: '/pages/index'
					   });
			      } 
			    }
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f8f8f8;
	}

	.copyright {
		margin-top: 50rpx;
		text-align: center;
		line-height: 60rpx;
		color: #999;
	}

	.header-section {
		display: flex;
		padding: 30rpx 0 0;
		flex-direction: column;
		align-items: center;
	}
.menu-item-box .completed-status {
  margin-left: auto;
  margin-right: 34rpx;
  color: green;
}

.menu-item-box .pending-status {
  margin-left: auto;
  margin-right: 34rpx;
  color: red;
}
</style>